import React, { useEffect, useState } from 'react';
import './sidebar.scss';
import { Link, useLocation } from 'react-router-dom';
import logo from '../../assets/images/vedanta_balco-removebg.png';
import logopower from '../../assets/images/xyma.png';
import sidebarNav from '../../configs/sidebarNav';

const Sidebar = () => {

    const [activeIndex, setActiveIndex] = useState(0);
    const location = useLocation();
    
    useEffect(() => {
        const curPath = window.location.pathname.split('/')[1];
        const activeItem = sidebarNav.findIndex(item => item.section === curPath);
        setActiveIndex(curPath.length === 0 ? 0 : activeItem);
    }, [location]);

    const closeSidebar = () => {
        document.querySelector('.main__content').style.transform = 'scale(1) translateX(0)';
        setTimeout(() => {
            document.body.classList.remove('sidebar-open');
            document.querySelector('.main__content').style = '';
        }, 500);
    }

    const logout = () => {
        localStorage.removeItem('token');
        window.location.href = '/';
        alert('Logout Successful');
    }

    return (
        <div className='sidebar'>
            <div className="sidebar__logo">
                <img src={logo} alt="" style={{width : '14rem',height : '4rem'}}/>
                <div className="sidebar-close" onClick={closeSidebar}>
                    <i className='bx bx-x'></i>
                </div>
            </div>
            <div className="sidebar__menu">
                {
                    sidebarNav.map((nav, index) => (
                        <Link to={nav.link} key={`nav-${index}`} className={`sidebar__menu__item ${activeIndex === index && 'active'}`} onClick={closeSidebar}>
                            <div className="sidebar__menu__item__icon">
                                {nav.icon}
                            </div>
                            <div className="sidebar__menu__item__txt">
                                {nav.text}
                            </div>
                        </Link>
                    ))
                }
                <div className="pl-10 mt-3">
                    
                    <img src={logopower} alt="powedbyLogo" style={{width : '9rem',height : '4rem', marginBottom: '1rem'}}/>
                    <span className='text-base font-extralight'> All rights reserved</span>
                </div>
                <div className="sidebar__menu__item" onClick={() => logout()}>
                    <div className="sidebar__menu__item__icon">
                        <i className='bx bx-log-out'></i>
                    </div>
                    <div className="sidebar__menu__item__txt">
                        Logout
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Sidebar
